ul.HeaderTabs {
    font: normal 16px / 150% "Sailec", sans-serif;
    list-style: none;
    width: 100%;
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    -webkit-overflow-scrolling: touch;
    white-space: nowrap;
    position: relative;
    top: calc(12px * 3);
}
@media (max-width: 34em) {
    ul.HeaderTabs {
    top: 12px;
    }
}
ul.HeaderTabs * {
        transition: all 0.15s linear;
    }
li.HeaderTabs__item {
        width: auto;
        display: inline-flex;
    }
