.Button {
    align-items: center;
    background: hsl(105, 52%, 58%);
    border-radius: 3px;
    display: flex;
    font: 600 16px / 150% "Sailec", sans-serif;
    line-height: 100%;
    justify-content: center;
    padding: 12px calc(12px * 3);
    text-align: center;
}
.Button:hover {
    background: hsl(105, 52%, 48%);
}
.Button__icon {
        margin-right: calc(12px / 2);
    }
.Button__title {
        color: hsl(0, 0%, 100%);
    }
.Button--cyan {
        background: hsl(190, 73%, 59%);
    }
.Button--cyan:hover {
        background: hsl(190, 73%, 49%);
    }
.Button--facebook {
        background: #3b5998;
    }
.Button--twitter {
        background: #00aced;
    }
.Button--transparent {
        background: rgba(0, 0, 0, 0.2);
        border: 2px solid hsl(0, 0%, 100%);
    }
.Button--transparent:hover {
        background: rgba(0, 0, 0, 0.3);
    }
.Button--narrow {
    display: inline-flex;
}
@media (max-width: 34em) {
    .Button--narrow {
    display: flex;
    }
}
.Button--large {
    font: 600 19px / 150% "Sailec", sans-serif;
    padding: calc(12px * 1.25) calc(12px * 3);
}

