.FormButton {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    align-items: center;
    background: hsl(105, 52%, 58%);
    border-radius: 3px;
    border: none;
    color: hsl(0, 0%, 100%);
    display: inline-flex;
    font: 600 16px / 150% "Sailec", sans-serif;
    line-height: 100%;
    justify-content: center;
    padding: 12px calc(12px * 3);
}
@media (max-width: 34em) {
    .FormButton {
    display: block;
    width: 100%;
    }
}
.FormButton:hover {
    background: hsl(105, 52%, 48%);
}
.FormButton--hidden {
    display: none;
}
.FormButton--wide {
    width: 100%;
}
.FormButton--large {
    font: 600 19px / 150% "Sailec", sans-serif;
    padding: calc(12px * 1.25) calc(12px * 3);
}
.FormButton[disabled] {
    opacity: 0.5;
}
.FormButton[disabled]:hover {
    background: hsl(105, 52%, 58%);
}
