.MetaLink__title {
        font: normal 14px / 150% "Sailec", sans-serif;
        color: hsl(204, 6%, 55%);
        padding-top: calc(12px / 2);
    }
a .MetaLink__title:hover {
        color: hsl(204, 6%, 40%);
    }
.MetaLink--white .MetaLink__title {
	color: hsl(0, 0%, 100%);
}
.MetaLink__title  a {
    color: hsl(205, 82%, 57%);
}
.MetaLink--white a .MetaLink__title:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
.MetaLink--large .MetaLink__title {
    font: normal 16px / 150% "Sailec", sans-serif;
}
.MetaLink--cyan .MetaLink__title {
    color: hsl(190, 73%, 59%);
}
.MetaLink--cyan a .MetaLink__title:hover {
    color: hsl(190, 73%, 49%);
}

