/* Legacy browsers (IE10+) */
.PhotoRow {
    position: relative;
    background: hsl(204, 6%, 88%);
}
.PhotoRow__row {
        display: flex;
    }
@media (max-width: 34em) {
    .PhotoRow__row {
        display: block;
    }
}
.PhotoRow__subRow {
        display: flex;
    }
.PhotoRow__actions {
        align-items: center;
        display: flex;
        height: 100%;
        justify-content: center;
        left: 0;
        pointer-events: none;
        position: absolute;
        top: 0;
        width: 100%;
    }
.PhotoRow__action  {
        pointer-events: auto;
        margin: 0 calc(12px / 2);
    }
