/* Legacy browsers (IE10+) */
.Header {
    background-position: center center;
    background-repeat: no-repeat;
    background-size: cover;
    padding-top: calc(12px * 2);
    padding-bottom: calc(12px * 3);
}
@media (max-width: 34em) {
    .Header {
    padding-bottom: 12px;
    }
}
.Header__content {
        margin-top: calc(12px * 5);
        margin-left: calc(12px * 5);
    }
@media (max-width: 34em) {
    .Header__content {
        margin-top: calc(12px * 3);
    }
}
@media (max-width: 56em) {
    .Header__content {
        margin-left: 0;
    }
}
.Header--high .Header__content {
     margin-top: calc(12px * 16);
}
