.Title__title {
    font: bold 30px / 120% "Sailec", sans-serif;
    color: hsl(204, 6%, 45%);
}
@media (max-width: 34em) {
    .Title__title {
    font: 600 21px / 135% "Sailec", sans-serif;
    }
}
a .Title__title:hover {
    color: hsl(204, 6%, 30%);
}
.Title__title > a {
    color: hsl(205, 82%, 57%);
}
.Title--small .Title__title {
    font: 600 21px / 135% "Sailec", sans-serif;
}
@media (max-width: 56em) {
    .Title--small .Title__title {
    font: 600 19px / 150% "Sailec", sans-serif;
    }
}
.Title--smaller .Title__title {
    font: 600 19px / 150% "Sailec", sans-serif;
    font-weight: bold;
}
.Title--large .Title__title {
    font: bold 48px / 100% "Sailec", sans-serif;
}
@media (max-width: 56em) {
    .Title--large .Title__title {
    font: bold 30px / 120% "Sailec", sans-serif;
    }
}
.Title--white .Title__title {
    color: hsl(0, 0%, 100%);
}
.Title--white a .Title__title:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
.Title--red .Title__title {
    color: hsl(0, 79%, 66%);
}
.Title--red a .Title__title:hover {
    color: hsl(0, 79%, 56%);
}
.Title--gray .Title__title {
    color: hsl(204, 6%, 55%);
}
.Title--gray a .Title__title:hover {
    color: hsl(204, 6%, 40%);
}
.Title--blue .Title__title {
    color: hsl(205, 82%, 57%);
}
.Title--blue a .Title__title:hover {
    color: hsl(205, 82%, 47%);
}
.Title--shadow .Title__title {
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
}
.Title--center .Title__title {
    text-align: center;
}
