/* Legacy browsers (IE10+) */
.BlogCard {
	padding: 0 calc(12px * 3);
	display: flex;
	flex-direction: column;
	align-items: center;
}
.BlogCard__title {
    	font: 600 21px / 135% "Sailec", sans-serif;
    	margin-bottom: 12px;
    	text-align: center;
        color: hsl(204, 6%, 45%);
    }
@media (max-width: 34em) {
	.BlogCard__title {
    	font: 600 19px / 150% "Sailec", sans-serif;
	}
}
.BlogCard__user {
    	display: flex;
    	align-items: center;
    }
.BlogCard__meta {
		padding: calc(12px / 2);
	}
