.DestinationBar {
    background: hsl(45, 100%, 48%);
    display: flex;
    height: calc(12px * 6);
    align-items: center;
    padding: 0 calc(12px * 1.5);
}
.DestinationBar__parents {
        color: hsl(0, 0%, 100%);
        display: flex;
        font: 600 19px / 150% "Sailec", sans-serif;
        align-items: center;
        margin-bottom: calc(12px / 3);
    }
.DestinationBar__parents > div {
        margin-right: calc(12px / 2);
    }
.DestinationBar__title {
        color: hsl(0, 0%, 100%);
        font: 600 21px / 135% "Sailec", sans-serif;
        line-height: 100%;
    }
.DestinationBar__title:hover {
        color: hsla(0, 0%, 100%, 0.9);
    }
.DestinationBar--purple {
    background: hsl(245, 51%, 68%);
}
.DestinationBar--red {
    background: hsl(0, 79%, 66%);
}
