.NavbarMobile {
    position: relative;
    z-index: 10000;
}
.NavbarMobile__menuIcon {
        cursor: pointer;
    }
.NavbarMobile__menuIcon svg {
        fill: hsl(204, 6%, 55%);
    }
.NavbarMobile__userImage {
        margin-left: 12px;
        display: flex;
        align-items: center;
    }
.NavbarMobile__badge {
        margin-right: calc(12px / 2);
    }
.NavbarMobile__menu {
        align-items: flex-start;
        background: hsl(105, 52%, 58%);
        bottom: 0;
        left: 0;
        padding: calc(12px * 2);
        position: fixed;
        right: 0;
        top: 0;
        z-index: 10000;
        overflow-y: scroll;
    }
.NavbarMobile__header {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
.NavbarMobile__search svg {
        fill: hsl(0, 0%, 100%);
    }
.NavbarMobile__closeIcon {
        cursor: pointer;
    }
.NavbarMobile__closeIcon svg {
        fill: hsl(0, 0%, 100%);
    }
.NavbarMobile__links {
        padding: 12px;
    }
.NavbarMobile__sublinkWrapper {
        padding: 12px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
.NavbarMobile__sublinkTitle {
        color: hsl(0, 0%, 100%);
        font: normal 19px / 150% "Sailec", sans-serif;
        text-align: center;
    }
.NavbarMobile__sublinkBadge {
        margin-left: 12px;
    }
.NavbarMobile--white .NavbarMobile__menuIcon svg {
    fill: hsl(0, 0%, 100%);
}
