/* Legacy browsers (IE10+) */
.TravelmateStart {
    border: 2px solid hsl(204, 6%, 88%);
    border-radius: 3px;
}
.TravelmateStart__fields {
    display: flex;
}
@media (max-width: 34em) {
    .TravelmateStart__fields {
    display: block;
    }
}
.TravelmateStart__field {
    padding: calc(12px / 2) 12px;
    color: hsl(204, 6%, 55%);
    border-right: 2px solid hsl(204, 6%, 88%);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    font: normal 14px / 150% "Sailec", sans-serif;
    cursor: pointer;
}
@media (max-width: 34em) {
    .TravelmateStart__field {
    border-right: none;
    border-bottom: 2px solid hsl(204, 6%, 88%);
    justify-content: flex-start;
    }
}
.TravelmateStart__field:last-child {
    border-right: none;
}
@media (max-width: 34em) {
    .TravelmateStart__field:last-child {
    border-bottom: none;
    }
}
.TravelmateStart__field:hover {
    background-color: hsl(204, 6%, 72%);
}
.TravelmateStart__field.TravelmateStart--active,
.TravelmateStart__field.TravelmateStart--active:hover {
    background-color: hsl(204, 6%, 55%);
    color: hsl(0, 0%, 100%);
}