.FormSelect {

}
.multiselect__content {
    overflow: auto;
}
.multiselect__option--highlight {
    background: hsl(29, 100%, 62%) !important;
}
.multiselect__option--highlight::after {
    content: none !important;
}
.multiselect__tags {
    font: normal 14px / 150% "Sailec", sans-serif !important;
}
.multiselect__tag {
    font: normal 13px / 150% "Sailec", sans-serif !important;
    background: none !important;
    border: 1px solid hsl(29, 100%, 62%) !important;
    border-radius: 12px !important;
    color: hsl(29, 100%, 62%) !important;
    padding: 1px 22px 0 10px !important;
}
.multiselect__tag span {
    color: hsl(29, 100%, 62%) !important;
}
.multiselect__tag-icon:hover {
    background: none !important;
}
.multiselect__tag-icon::after {
    color: hsl(29, 100%, 62%) !important;
}
.multiselect__content {
    font: normal 14px / 150% "Sailec", sans-serif;
}
