/* Legacy browsers (IE10+) */
.Block__title {
    margin-bottom: calc(12px * 2);
}
.Block--gray {
    background: hsl(204, 6%, 97%);
    padding: calc(12px * 2);
}
@media (max-width: 34em) {
    .Block--gray {
    padding: 12px;
    }
}
.Block--border {
    border: 2px solid hsl(204, 6%, 88%);
    border-radius: 3px;
    padding: calc(12px * 2);
}
@media (max-width: 34em) {
    .Block--border {
    padding: 12px;
    }
}
.Block--inset {
    margin-left: calc(12px * 7);
}
@media (max-width: 34em) {
    .Block--inset {
    margin-left: 0;
    }
}
