.FormRadio {
    display: flex;
}
@media (max-width: 56em) {
    .FormRadio {
    display: block;
    }
}
.FormRadio__option {
        display: flex;
        margin-right: 12px;
    }
@media (max-width: 56em) {
    .FormRadio__option {
        margin-right: 0;
        margin-bottom: 12px;
    }
}
.FormRadio__option:last-child {
        margin-right: 0;
    }
.FormRadio__input {
        display: block;
        margin-right: calc(12px / 2);
    }
.FormRadio__label {
        font: 600 16px / 150% "Sailec", sans-serif;
        color: hsl(204, 6%, 55%);
    }
