.UserImage {
    position: relative;
}
.UserImage__image {
    border-radius: 50%;
    width: 100%;
    height: 100%;
}
.UserImage__arcRank,
.UserImage__arcReminder {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
}
.UserImage__arcRank svg {
    stroke: hsl(190, 73%, 59%);
}
.UserImage__arcReminder svg {
    stroke: hsl(204, 6%, 88%);
}
.UserImage--dark .UserImage__arcRank svg {
    stroke: hsl(193, 100%, 34%);
}
