.Navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: calc(12px * 4);
}
.Navbar__logo {
        margin-left: calc(12px * -4.5);
        margin-top: calc(12px * 2);

    }
.Navbar__sitename {
        display: none;
    }
.Navbar--white .Navbar__logo svg {
        fill: hsl(0, 0%, 100%);
    }
.Navbar__right {
        display: flex;
        align-items: center;
    }
.Navbar__search {
        display: block;
    }
@media (max-width: 56em) {
    .Navbar__search {
        display: none;
    }
}
.Navbar__search svg {
        fill: hsl(204, 6%, 88%);
    }
.Navbar__desktop {
        display: block;
    }
@media (max-width: 56em) {
    .Navbar__desktop {
        display: none;
    }
}
.Navbar__mobile {
        display: none;
    }
@media (max-width: 56em) {
    .Navbar__mobile {
        display: block;
    }
}
