.Tag {
    align-items: center;
    border: 1px solid hsl(204, 6%, 55%);
    border-radius: 12px;
    display: inline-flex;
    font: normal 13px / 150% "Sailec", sans-serif;
    line-height: 100%;
    justify-content: center;
    padding: calc(12px / 4) calc(12px / 2) calc(12px / 8) calc(12px / 2);
}
.Tag__title {
        color: hsl(204, 6%, 55%);
        white-space: nowrap;
    }
.Tag__title:hover {
        color: hsl(204, 6%, 40%);
    }
.Tag--orange {
    border: 1px solid hsl(29, 100%, 62%);
}
.Tag--orange .Tag__title {
    color: hsl(29, 100%, 62%);
}
.Tag--orange .Tag__title:hover {
    color: hsl(29, 100%, 52%);
}
.Tag--red {
    border: 1px solid hsl(0, 79%, 66%);
    background: hsl(0, 79%, 66%);
}
.Tag--red:hover {
    background: hsl(0, 79%, 56%);
}
.Tag--red .Tag__title {
    color: hsl(0, 0%, 100%);
}
.Tag--white {
    border: 1px solid hsl(0, 0%, 100%);
    background: rgba(0, 0, 0, 0.05);
}
.Tag--white:hover {
    border: 1px solid hsl(0, 0%, 100%);
    background: rgba(0, 0, 0, 0.1);
}
.Tag--white .Tag__title {
    color: hsl(0, 0%, 100%);
}
.Tag--large {
    padding: calc(12px / 2) 12px;
    border-radius: calc(12px * 1.5);
}
