.FormLink {
    background: none;
    border: none;
    color: hsl(204, 6%, 55%);
    cursor: pointer;
    font: normal 14px / 150% "Sailec", sans-serif;
    padding-top: calc(12px / 2);
}
.FormLink {
    color: hsl(204, 6%, 55%);
}
.FormLink:hover {
    color: hsl(204, 6%, 40%);
}
.FormLink--white {
    color: hsl(0, 0%, 100%);
}
.FormLink--white:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
