.PhotoFullscreen {
    background: rgba(0, 0, 0, 0.8);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
}
.PhotoFullscreen__photo {
    bottom: 0;
    left: 0;
    margin: auto;
    max-height: 100%;
    max-width: 100%;
    overflow: auto;
    position: fixed;
    right: 0;
    top: 0;
}
.PhotoFullscreen__meta {
    bottom: 0;
    left: 0;
    right: 0;
    position: fixed;
    font: normal 16px / 150% "Sailec", sans-serif;
    color: hsl(0, 0%, 100%);
    text-align: center;
    margin: 0 calc(12px * 16) calc(12px * 2) calc(12px * 16);
    text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
}
@media (max-width: 56em) {
    .PhotoFullscreen__meta {
    margin: 0 calc(12px * 2) calc(12px * 2) calc(12px * 2);
    }
}
