.FormTextarea__label {
        color: hsl(204, 6%, 55%);
        display: block;
        font: 600 16px / 150% "Sailec", sans-serif;
        line-height: 135%;
        margin-bottom: 12px;
    }
.FormTextarea__textarea {
        border-radius: 3px;
        border: 2px solid hsl(204, 6%, 88%);
        font: normal 16px / 150% "Sailec", sans-serif;
        padding: 12px;
        width: 100%;
        color: hsl(204, 6%, 45%);
    }
.FormTextarea__textarea::placeholder {
        color: hsl(204, 6%, 88%);
    }
.FormTextarea--borderless .FormTextarea__textarea {
    border: none;
}
.FormTextarea__textarea[readonly] {
    background: hsl(204, 6%, 97%);
    color: hsl(204, 6%, 55%);
    opacity: 0.5;
}
.FormTextarea--hidden {
    display: none;
}
