.FormPassword__label {
        color: hsl(204, 6%, 55%);
        display: block;
        font: 600 16px / 150% "Sailec", sans-serif;
        margin-bottom: 12px;
    }
.FormPassword__input {
        border-radius: 3px;
        border: 2px solid hsl(204, 6%, 88%);
        color: hsl(204, 6%, 45%);
        font: normal 16px / 150% "Sailec", sans-serif;
        padding: 12px;
        width: 100%;
        letter-spacing: 0.1rem;
    }
.FormPassword__input::placeholder {
        color: hsl(204, 6%, 88%);
    }
.FormPassword--large .FormPassword__input {
    font: 600 19px / 150% "Sailec", sans-serif;
    padding: 12px;
}
