.FontExperiment__label {
        font-family: monospace;
        margin-bottom: 12px;
        color: hsl(204, 6%, 88%);
    }
.FontExperiment__text:hover {
        background: yellow;
    }
.FontExperiment__text {
        margin-bottom: calc(12px * 3);
    }
.FontExperiment__textxs {
        font: normal 13px / 150% "Sailec", sans-serif;
    }
.FontExperiment__textsm {
        font: normal 14px / 150% "Sailec", sans-serif;
    }
.FontExperiment__textmd {
        font: normal 16px / 150% "Sailec", sans-serif;
    }
.FontExperiment__textlg {
        font: normal 19px / 150% "Sailec", sans-serif;
    }
.FontExperiment__heading {
        margin-bottom: calc(12px * 3);
    }
.FontExperiment__heading:hover {
        background: yellow;
    }
.FontExperiment__headingxs {
        font: 600 14px / 150% "Sailec", sans-serif;
    }
.FontExperiment__headingsm {
        font: 600 16px / 150% "Sailec", sans-serif;
    }
.FontExperiment__headingmd {
        font: 600 19px / 150% "Sailec", sans-serif;
    }
.FontExperiment__headinglg {
        font: 600 21px / 135% "Sailec", sans-serif;
    }
.FontExperiment__headingxl {
        font: bold 30px / 120% "Sailec", sans-serif;
    }
.FontExperiment__headingxxl {
        font: bold 48px / 100% "Sailec", sans-serif;
    }
.FontExperiment__headingxxxl {
        font: bold 64px / 100% "Sailec", sans-serif;
    }
.FontExperiment__button {
        border: 1px solid black;
        border-radius: 100px;
        padding: 0 0.5em;
    }
