.Badge {
    background: hsl(0, 79%, 66%);
    align-items: center;
    border-radius: 12px;
    display: inline-flex;
    font: normal 13px / 150% "Sailec", sans-serif;
    justify-content: center;
    padding: calc(12px / 2) calc(12px / 1.5) calc(12px / 4) calc(12px / 1.5);
}
.Badge__title {
        font: normal 13px / 150% "Sailec", sans-serif;
        color: hsl(0, 0%, 100%);
    }
.Badge--white {
     background: hsl(0, 0%, 100%);
}
.Badge--white .Badge__title {
     color: hsl(105, 52%, 58%);
}

