.NavbarDesktop {
    position: relative;
    z-index: 1000;
}
.NavbarDesktop__links {
        display: flex;
    }
.NavbarDesktop__link {
        padding: 12px;
        color: hsl(204, 6%, 55%);
        font: 600 16px / 150% "Sailec", sans-serif;
        white-space: nowrap;
    }
.NavbarDesktop__link:hover {
        color: hsl(204, 6%, 40%);
    }
.NavbarDesktop__userImage {
        margin-left: 12px;
        display: flex;
        align-items: center;
    }
.NavbarDesktop__badge {
        margin-right: calc(12px / 2);
    }
.NavbarDesktop__popover {
        width: calc(12px * 15);
        position: absolute;
        right: 0;
        top: calc(12px * 4);
    }
.NavbarDesktop__arrowWrapper {
        display: flex;
        justify-content: flex-end;
        padding-right: calc(12px * 5);
    }
.NavbarDesktop__arrow {
        width: 0;
        height: 0;
        border-left: calc(12px / 2) solid transparent;
        border-right: calc(12px / 2) solid transparent;
        border-bottom: calc(12px / 2) solid hsl(105, 52%, 58%);
    }
.NavbarDesktop__sublinks {
        border-radius: 3px;
        background: hsl(105, 52%, 58%);
        padding: calc(12px / 2) 0;
    }
.NavbarDesktop__sublinkWrapper {
        padding: 12px calc(12px * 2);
        display: flex;
        align-items: center;
    }
.NavbarDesktop__sublinkWrapper:hover {
        background: #288f18;
    }
.NavbarDesktop__sublinkTitle {
        color: hsl(0, 0%, 100%);
        font: 600 16px / 150% "Sailec", sans-serif;
    }
.NavbarDesktop__sublinkBadge {
        margin-left: 12px;
    }
.NavbarDesktop--white .NavbarDesktop__link {
    color: hsl(0, 0%, 100%);
}
.NavbarDesktop--white .NavbarDesktop__link:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
