/* Legacy browsers (IE10+) */
.BlockHorizontal {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
@media (max-width: 34em) {
    .BlockHorizontal {
    display: block;
    }
}
.BlockHorizontal__item {
    margin-right: 12px;
}
@media (max-width: 34em) {
    .BlockHorizontal__item {
    margin-right: 0;
    }
}
@media (max-width: 56em) {
    .BlockHorizontal__item {
    margin-bottom: 12px;
    }
}
.BlockHorizontal--center {
    justify-content: center;
}
.BlockHorizontal--right {
    justify-content: flex-end;
}
.BlockHorizontal--between {
    justify-content: space-between;
}
.BlockHorizontal--bottom {
    align-items: flex-end;
}
