.FormButtonProcess {
    -webkit-appearance: none;
    -webkit-border-radius: 0;
    align-items: center;
    background: hsl(105, 52%, 58%);
    border-radius: 3px;
    border: none;
    color: hsl(0, 0%, 100%);
    display: inline-flex;
    font: 600 16px / 150% "Sailec", sans-serif;
    justify-content: center;
    padding: 12px calc(12px * 3);
}
@media (max-width: 34em) {
    .FormButtonProcess {
    display: block;
    width: 100%;
    }
}
.FormButtonProcess[disabled] {
    background: hsl(204, 6%, 88%);
}
.FormButtonProcess:hover {
    background: hsl(105, 52%, 48%);
}
