.BlockTitle {
    display: flex;
    align-items: center;
}
.BlockTitle__title {
        color: hsl(105, 52%, 58%);
        font: 600 14px / 150% "Sailec", sans-serif;
        text-transform: uppercase;
    }
a .BlockTitle__title:hover {
        color: hsl(105, 52%, 48%);
    }
.BlockTitle__link {
        margin-left: calc(12px / 2);
    }
.BlockTitle--yellow .BlockTitle__title {
    color: hsl(45, 100%, 48%);
}
.BlockTitle--yellow a .BlockTitle__title:hover {
    color: hsl(45, 100%, 38%);
}
.BlockTitle--cyan .BlockTitle__title {
    color: hsl(190, 73%, 59%);
}
.BlockTitle--cyan a .BlockTitle__title:hover {
    color: hsl(190, 73%, 49%);
}
