.NavbarSearch {
	display: flex;
	align-items: center;
}
.NavbarSearch__icon {
		cursor: pointer;
		margin-right: 12px;
	}
.NavbarSearch__input {
		background: none;
		border: none;
        font: 600 16px / 150% "Sailec", sans-serif;
		padding: calc(12px / 2);
	}
.NavbarSearch__input:focus {
	    outline: none;
	}
.NavbarSearch--white {
	fill: hsl(0, 0%, 100%);
}
.NavbarSearch--white .NavbarSearch__input {
		color: hsl(0, 0%, 100%);
	}
.NavbarSearch--gray {
	fill: hsl(204, 6%, 45%);
}
