.PromoBar {
    background: hsl(205, 82%, 57%);
    padding: 12px;
    font: normal 16px / 150% "Sailec", sans-serif;
    display: flex;
    justify-content: space-between;
}
.PromoBar__titleWrapper {
        display: flex;
        flex-wrap: wrap;
    }
.PromoBar__title {
        color: hsl(0, 0%, 100%);
        margin-right: 12px;
    }
@media (max-width: 34em) {
    .PromoBar__title {
        margin-bottom: calc(12px / 2);
    }
}
.PromoBar__route {
        color: hsl(0, 0%, 100%);
        text-decoration: underline;
    }
.PromoBar__closeIcon svg {
        fill: hsl(0, 0%, 100%);
    }
