/* Legacy browsers (IE10+) */
.Alert {
    background: hsl(0, 0%, 100%);
    border-radius: 3px;
    border: 2px solid hsl(105, 52%, 58%);
    bottom: calc(12px * 3);
    display: flex;
    justify-content: space-between;
    left: calc(12px * 3);
    padding: 12px;
    position: fixed;
    width: calc(12px * 30);
}
@media (max-width: 34em) {
    .Alert {
    bottom: 12px;
    left: 12px;
    right: 12px;
    width: inherit;
    }
}
.Alert__title {
        color: hsl(204, 6%, 45%);
        font: normal 16px / 150% "Sailec", sans-serif;
    }
.Alert__closeIcon {
        margin-left: calc(12px / 2);
    }
.Alert__closeIcon svg {
        fill: hsl(204, 6%, 45%);
    }
