.Footer {
    background-size: cover;
    padding: calc(12px * 7) 0 calc(12px * 3) 0;
}
.Footer__wrapper {
        display: flex;
        justify-content: center;
        margin-bottom: calc(12px * 7);
    }
@media (max-width: 56em) {
    .Footer__wrapper {
        flex-direction: column;
    }
}
.Footer__col {
        flex-shrink: 0;
        width: 25%;
    }
@media (max-width: 56em) {
    .Footer__col {
        text-align: center;
        width: inherit;
    }
}
.Footer__link {
        color: white;
        font: 600 16px / 150% "Sailec", sans-serif;
        padding: calc(12px / 2) 0;
    }
@media (max-width: 34em) {
    .Footer__link {
        padding: 12px 0;
    }
}
.Footer__link:hover {
        color: hsla(0, 0%, 100%, 0.9);
    }
.Footer__social {
        display: flex;
        justify-content: center;
        text-align: center;
    }
@media (max-width: 34em) {
    .Footer__social {
        flex-direction: column;
        justify-content: left;
    }
}
.Footer__socialLink {
        color: white;
        font: 600 16px / 150% "Sailec", sans-serif;
        margin-right: calc(12px * 2);
        padding: calc(12px / 2);
        line-height: 220%;
    }
.Footer__socialLink:hover {
        color: hsla(0, 0%, 100%, 0.9);
    }
.Footer__socialIcon {
    }
@media (max-width: 34em) {
    .Footer__socialIcon {
        display: none;
    }
}
.Footer__licence {
        color: hsl(204, 6%, 88%);
        font: normal 16px / 150% "Sailec", sans-serif;
        height: calc(12px * 4);
        margin-top: calc(12px * 3);
        text-align: center;
    }
.Footer--light .Footer__link {
    color: hsl(204, 6%, 55%);
}
.Footer--light .Footer__link:hover {
    color: hsl(204, 6%, 40%);
}
.Footer--light .Footer__socialLink {
    color: hsl(204, 6%, 55%);
}
.Footer--light .Footer__socialLink:hover {
    color: hsl(204, 6%, 40%);
}
.Footer--light .Footer__licence {
    color: hsl(204, 6%, 55%);
}
.Footer--light .Footer__socialIcon svg {
    fill: hsl(204, 6%, 55%);
}
