.AuthTab {
    background: hsl(0, 0%, 100%);
    height: calc(12px * 4);
    display: flex;
    align-items: center;
    justify-content: center;
}
.AuthTab__title {
        color: hsl(205, 82%, 57%);
        font: 600 19px / 150% "Sailec", sans-serif;
    }
.AuthTab--facebook {
    background: #3b5998;
}
.AuthTab--facebook .AuthTab__title {
    color: white;
}
.AuthTab--google {
    background: #dd4b39;
}
.AuthTab--google .AuthTab__title {
    color: white;
}
