/* Legacy browsers (IE10+) */
.HeaderLight {
    padding-top: calc(12px * 2);
    padding-bottom: calc(12px * 2);
}
@media (max-width: 34em) {
    .HeaderLight {
    padding-bottom: calc(12px / 2);
    }
}
.HeaderLight__content {
        margin-top: calc(12px * 5);
        margin-left: calc(12px * 5);
    }
@media (max-width: 56em) {
    .HeaderLight__content {
        margin-left: 0;
    }
}
.HeaderLight__meta {
        margin-top: calc(12px * 2);
    }
.HeaderLight__meta2 {
        margin-top: calc(12px * 2);
    }
.HeaderLight__title {
        color: hsl(204, 6%, 45%);
        font: bold 48px / 100% "Sailec", sans-serif;
    }
@media (max-width: 34em) {
    .HeaderLight__title {
        font: bold 30px / 120% "Sailec", sans-serif;
    }
}
