.FrontpageSearchRow {
    float: left;
    width: 100%;
    position: relative;
    margin-top: 12px;
    min-height: 32px;
}
.FrontpageSearchRow__title {
        float: left;
        color: hsl(0, 79%, 66%);
        padding-left: calc(12px * 4);
        padding-bottom: calc(12px / 2);
        text-transform: uppercase;
        font-size: 10px;
        font-weight: bold;
    }
.FrontpageSearchRow__icon {
        padding-left: 12px;
        padding-top: 0;
        position: absolute;
        top: 0;
        left: 0;
        cursor: pointer;
    }
@media (max-width: 34em) {
    .FrontpageSearchRow__icon {
        display: none;
    }
}
.FrontpageSearchRow__icon svg {
            fill: hsl(204, 6%, 88%);
        }
.FrontpageSearchRow__footer {
        width: 100%;
        float: left;
        padding-left: calc(12px * 4);
        position: relative;
        background: hsl(0, 0%, 100%);
        min-height: 32px;
        color: hsl(205, 82%, 57%);
        font-weight: 600;
        font-size: 13px;
        padding-top: calc(12px / 2);
    }
.FrontpageSearchRow__footer::before {
            content: '';
            position: absolute;
            top: -54px;
            left: 0;
            display: block;
            width: 100%;
            height: 54px;
            background-image: linear-gradient(to bottom,rgba(255,255,255,0),#fff);
            pointer-events: none;
        }
.FrontpageSearchRow__footer svg {
                fill: hsl(204, 6%, 88%);
                display: inline-block;
                margin-left: calc(12px / 3);
                position: relative;
                top: 2px;
            }
