.FrontpageSearch {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
    font-family: "Sailec", "Cousine";
}
.FrontpageSearch * {
        transition: all 0.15s linear;
    }
.FrontpageSearch__search {
    width: 100%;
    position: relative;
}
.FrontpageSearch__input {
    background: rgba(255, 255, 255, 0.85);
    outline: 0;
    -webkit-appearance: none;
    font: 600 19px / 150% "Sailec", sans-serif;
    padding: 5px 40px calc(12px / 3) 50px;
    color: #000;
    border: 0;
    width: 100%;
    height: 50px;
}
.FrontpageSearch:hover .FrontpageSearch__input,
    .FrontpageSearch__input:focus {
        background: hsl(0, 0%, 100%);
    }
.FrontpageSearch ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
        color: hsl(204, 6%, 45%);
    }
.FrontpageSearch :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
        color: hsl(204, 6%, 45%);
        opacity:  1;
    }
.FrontpageSearch ::-moz-placeholder { /* Mozilla Firefox 19+ */
        color: hsl(204, 6%, 45%);
        opacity: 1;
    }
.FrontpageSearch :-ms-input-placeholder { /* Internet Explorer 10-11 */
        color: hsl(204, 6%, 45%);
    }
.FrontpageSearch ::-ms-input-placeholder { /* Microsoft Edge */
        color: hsl(204, 6%, 45%);
    }
.FrontpageSearch__icon {
    padding-left: 12px;
    padding-top: 3px;
    position: absolute;
    top: 8px;
    left: 0;
    cursor: pointer;
}
@media (max-width: 34em) {
    .FrontpageSearch__icon {
    display: none;
    }
}
.FrontpageSearch__icon svg {
        fill: hsl(0, 79%, 66%);
    }
.FrontpageSearch__input:hover + .FrontpageSearch__icon svg,
    .FrontpageSearch__input:focus + .FrontpageSearch__icon svg,
    .FrontpageSearch__icon:hover svg {
        fill: hsl(0, 79%, 56%);
    }
.FrontpageSearch__loading {
    position: absolute;
    right: 1px;
    top: 1px;
    width: 50px;
    height: 48px;
}
.FrontpageSearch__loading::before,
    .FrontpageSearch__loading::after {
        position: absolute;
        content: "";
        top: 50%;
        left: 50%;
        margin: -8px 0 0 -8px;
        width: 16px;
        height: 16px;
        border-radius: 100%;
        border-color: #288f18 transparent transparent;
        border-style: solid;
        border-width: 2px;
        box-shadow: 0 0 0 1px transparent;
    }
.FrontpageSearch__loading::before {
        animation: spinning 2.4s cubic-bezier(.41,.26,.2,.62);
        animation-iteration-count: infinite;
    }
.FrontpageSearch__loading::after {
        animation: spinning 2.4s cubic-bezier(.51,.09,.21,.8);
        animation-iteration-count: infinite;
    }
.FrontpageSearch__results {
    width: 100%;
    position: absolute;
    top: 50px;
    background: hsl(204, 6%, 97%);
    height: auto;
    left: 0;
    z-index: 2;
    box-shadow: 0 18px 48px rgba(14,18,23,.2);
}
