.HeaderTab {
    color: hsl(0, 0%, 100%);
    padding: calc(12px * 1.5);
    display: block;
}
.HeaderTab:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
.HeaderTab__count {
        border-radius: calc(12px * 4);
        color: hsl(0, 0%, 100%);
        background: hsl(204, 6%, 55%);
        padding: 4px 9px 1px;
        display: inline-block;
        margin-left: calc(12px / 4);
    }
.HeaderTab:hover,
.HeaderTab__active {
    display: block;
    color: hsl(205, 82%, 57%);
    background: hsl(0, 0%, 100%);
}
.HeaderTab__active:hover {
    color: hsl(205, 82%, 47%);
    background: hsla(0, 0%, 100%, 0.9);
}
.HeaderTab:hover .HeaderTab__count,
    .HeaderTab__active .HeaderTab__count {
        background: hsl(204, 6%, 88%);
        color: hsl(204, 6%, 45%);
    }