.FrontpageSearchDestination {
    background: rgba(255, 255, 255, 0.85);
    display: flex;
    align-items: center;
}
.FrontpageSearchDestination__icon {
        padding-left: 12px;
    }
@media (max-width: 34em) {
    .FrontpageSearchDestination__icon {
        display: none;
    }
}
.FrontpageSearchDestination__icon svg {
        fill: hsl(0, 79%, 66%);
    }
.FrontpageSearchDestination__search {
        width: 100%;
    }
.FrontpageSearchDestination .multiselect__tags {
        border: none;
        background: none;
    }
.FrontpageSearchDestination .multiselect__input {
        font: 600 19px / 150% "Sailec", sans-serif;
        background: none;
        padding: calc(12px / 3) 0 calc(12px / 3) calc(12px / 3);
        color: hsl(204, 6%, 45%);
    }
