.Body {
    font: normal 16px / 150% "Sailec", sans-serif;
    line-height: 150%;
}
.Body,
.Body * {
    color: hsl(204, 6%, 45%);
}
.Body p {
    margin-bottom: calc(12px * 2);
}
.Body p:last-child {
    margin-bottom: 0;
}
.Body h1,
.Body h2,
.Body h3 {
    margin: 12px 0 12px 0;
    font: 600 21px / 135% "Sailec", sans-serif;
}
.Body h4 {
    margin: 12px 0 12px 0;
    font: 600 19px / 150% "Sailec", sans-serif;
    color: hsl(204, 6%, 55%);
}
.Body a,
.Body a:link,
.Body a:active {
    color: hsl(205, 82%, 57%);
    text-decoration: underline;
    word-wrap: break-word;
    word-break: break-word;
}
.Body a:hover {
    color: hsl(205, 82%, 47%);
}
.Body ul {
    padding-left: calc(12px * 2);
    margin: calc(12px * 2) 0;
}
.Body ul li {
    list-style-type: disc;
}
/*
.Body ol li {
    list-style-type: decimal;
}
*/
.Body ol {
    list-style-type: none;
    position: relative;
    margin: calc(12px * 2) 0;
}
.Body ol li {
    counter-increment: step-counter;
    margin: 0 0 calc(12px / 1.5) calc(12px * 2.25);
}
.Body ol li::before {
    padding-top: calc(12px * 0.2);
    background-color: hsl(204, 6%, 55%);
    border-radius: calc(12px * 100);
    color: hsl(0, 0%, 100%);
    content: counter(step-counter);
    font: normal 14px / 150% "Sailec", sans-serif;
    height: calc(12px * 1.75);
    left: 0;
    position: absolute;
    text-align: center;
    width: calc(12px * 1.75);
}
.Body blockquote {
    padding-left: calc(12px * 2);
    border-left: 4px solid hsl(204, 6%, 88%);
    font: normal 19px / 150% "Sailec", sans-serif;
    line-height: 150%;
    margin-bottom: calc(12px * 2);
    color: hsl(204, 6%, 88%);
}
.Body img {
    width: 100%;
}
.Body p img {
    margin-bottom: calc(12px / 3);
}
.Body table {
    width: 100%;
    border: 1px solid hsl(204, 6%, 88%);
    border-collapse: collapse;
    margin: calc(12px * 2) 0;
}
.Body th,
.Body tr {
    border-bottom: 1px solid hsl(204, 6%, 88%);
    text-align: left;
    vertical-align: top;
}
.Body th {
    background: hsl(204, 6%, 88%);
    font: 600 16px / 150% "Sailec", sans-serif;
}
.Body th,
.Body td {
    padding: calc(12px / 2);
    border-right: 1px solid #;
}
.Body hr {
    border-top: 1px solid hsl(204, 6%, 88%);
    border: 0;
    height: 0;
    margin: calc(12px * 2) 0;
}
.Body--responsive {
    font-family: "Sailec";
    font-size: calc(16px + 3 * ((100vw - 420px) / 860));
    line-height: 150%;
}
@media screen and (min-width: 1280px) {
    .Body--responsive {
        font-size: 19px;
    }
}
@media screen and (max-width: 420px) {
    .Body--responsive {
        font-size: 16px;
    }
}
.Body--white,
.Body--white * {
    color: hsl(0, 0%, 100%);
}
.Body--white a,
.Body--white a:link,
.Body--white a:active {
    color: hsl(0, 0%, 100%);
}
.Body--gray,
.Body--gray * {
    color: hsl(204, 6%, 55%);
}
