.SearchRow {
    width: 100%;
    float: left;
    border-bottom: 1px solid hsl(204, 6%, 88%);
    padding: calc(12px * 2) 0;
    margin: 0 0 12px;
}
.SearchRow:last-child {
    border-bottom: 0;
}
.SearchRow__header {
        width: 100%;
        float: left;
    }
.SearchRow__title {
            float: left;
            width: auto;
            font-size: 19px;
            font-weight: 600;
            position: relative;
            top: -4px;
        }
.SearchRow__badge {
            float: left;
            width: auto;
            margin-left: 12px;
            background: hsl(0, 79%, 66%);
            color: hsl(0, 0%, 100%);
            height: 22px;
            padding: 0 calc(12px / 2);
            border-radius: 20px;
            min-width: 26px;
            line-height: 26px;
            font-size: 14px;
            text-align: center;
            position: relative;
            top: -2px;
            font-weight: 600;
        }
.SearchRow__date {
            float: right;
            color: hsl(204, 6%, 55%);
            font-size: 14px;
        }
.SearchRow__main {
        width: 100%;
        float: left;
        margin-top: 12px;
    }
.SearchRow__image {
            display: block;
            float: left;
            height: 28px;
            width: 28px;
            border-radius: 50%;
        }
.SearchRow__image_alt {
            width: 96px;
            height: 65px;
            display: block;
            float: left;
        }
.SearchRow__image + .SearchRow__body,
        .SearchRow__image + .SearchRow__title {
            margin-left: calc(12px * 3.5);
            float: none;
            display: block;
        }
.SearchRow__image + .SearchRow__title {
            margin-top: calc(12px / 2);
        }
.SearchRow__image_alt + .SearchRow__body,
        .SearchRow__image_alt + .SearchRow__title {
            margin-left: calc(12px * 10);
            float: none;
            display: block;
        }
.SearchRow__body {
            display: block;
            color: hsl(204, 6%, 55%);
            font-size: 14px;
            line-height: 150%;
        }
