.NewsCard__image {
        background-repeat: no-repeat;
        background-size: cover;
        height: 0;
        max-width: 100%;
        overflow: hidden;
        padding-bottom: 56%;
        position: relative;
        margin-bottom: 12px;
    }
.NewsCard__title {
        color: hsl(205, 82%, 57%);
        font: 600 19px / 150% "Sailec", sans-serif;
        margin-bottom: calc(12px / 2);
    }
.NewsCard__title:hover {
        color: hsl(205, 82%, 47%);
    }
