.ImagePicker {
    position: fixed;
    background: rgba(0, 0, 0, 0.8);
    top: 0;
    bottom: 0;
    right: 0;
    padding: calc(12px * 2);
    width: calc(12px * 17);
    overflow-y: scroll;
    z-index: 10001;
}
.ImagePicker__close {
        position: fixed;
        top: calc(12px / 2);
        right: 12px;
        color: hsl(0, 0%, 100%);
        font-size: 2rem;
    }
.ImagePicker__card {
        margin-top: 12px;
    }
.ImagePicker__image {
        width: 100%;
        margin-bottom: calc(12px / 3);
    }
.ImagePicker__title {
        font: normal 14px / 150% "Sailec", sans-serif;
        color: hsl(0, 0%, 100%);
    }
