.Comment {
    display: flex;
    border: 2px solid hsl(204, 6%, 88%);
    border-radius: 3px;
    padding: calc(12px * 2);
}
@media (max-width: 34em) {
    .Comment {
    padding: 12px;
    }
}
.Comment__left {
        padding-right: 12px;
        margin-left: calc(12px * -3.5);
        margin-top: calc(12px * -0.75);
    }
@media (max-width: 34em) {
    .Comment__left {
        display: none;
    }
}
.Comment__meta {
        margin-bottom: 12px;
    }
.Comment--pink {
    background: hsl(7, 100%, 96%);
}
.Comment--inset {
    margin-left: calc(12px * 7);
}
@media (max-width: 34em) {
    .Comment--inset {
    margin-left: 0;
    }
}
.Comment__right {
    width: 100%;
}
