/* Legacy browsers (IE10+) */
.FlightRow {
    display: flex;
}
.FlightRow__left {
        display: flex;
        align-items: top;
        padding-right: 12px;
    }
@media (max-width: 34em) {
    .FlightRow__left {
        display: none;
    }
}
.FlightRow__right {
        display: flex;
        align-items: center;
    }
.FlightRow__title {
        color: hsl(205, 82%, 57%);
        font: 600 21px / 135% "Sailec", sans-serif;
        margin-bottom: calc(12px / 3);
    }
.FlightRow__title:hover {
        color: hsl(205, 82%, 47%);
    }

