/* Legacy browsers (IE10+) */
.NewsHeader {
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    padding: calc(12px * 2) 0;
}
.NewsHeader__content {
        margin-top: calc(12px * 25);
    }
@media (max-width: 34em) {
    .NewsHeader__content {
        margin-top: calc(12px * 15);
    }
}
.NewsHeader__title {
        color: hsl(0, 0%, 100%);
        font: bold 30px / 120% "Sailec", sans-serif;
        margin-bottom: 12px;
        text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
    }
@media (max-width: 34em) {
    .NewsHeader__title {
        font: 600 21px / 135% "Sailec", sans-serif;
    }
}
