.Link {
	display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.Link__title {
        color: hsl(204, 6%, 55%);
        font: normal 16px / 150% "Sailec", sans-serif;
    }
.Link__title:hover {
        color: hsl(204, 6%, 40%);
    }
.Link__icon {
        margin-left: calc(12px / 2);
    }
.Link__icon svg {
        fill: hsl(204, 6%, 55%);
    }
.Link--large .Link__title {
    font: 600 19px / 150% "Sailec", sans-serif;
}
.Link--blue .Link__title {
    color: hsl(205, 82%, 57%);
}
.Link--blue .Link__title:hover {
    color: hsl(205, 82%, 47%);
}
.Link--white .Link__title {
    color: hsl(0, 0%, 100%);
}
.Link--white .Link__title:hover {
    color: hsla(0, 0%, 100%, 0.9);
}
.Link--white .Link__icon svg {
    fill: hsl(0, 0%, 100%);
}
