.Flag {
    display: flex;
    align-items: baseline;
    padding-top: calc(12px / 2);
}
.Flag__icon {
        margin-right: calc(12px / 3);
    }
.Flag__icon:hover {
        cursor: pointer;
    }
.Flag__icon svg {
        fill: hsl(204, 6%, 88%);
    }
.Flag__value {
        font: normal 14px / 150% "Sailec", sans-serif;
        color: hsl(204, 6%, 55%);
        margin-right: calc(12px / 3);
    }
.Flag--green.Flag--unflagged .Flag__icon svg,
.Flag--red.Flag--unflagged .Flag__icon svg {
    fill: hsl(204, 6%, 55%);
}
.Flag--green.Flag--unflagged .Flag__icon:hover svg,
.Flag--red.Flag--unflagged .Flag__icon:hover svg {
    fill: hsl(204, 6%, 40%);
}
.Flag--green.Flag--unflagged .Flag__value,
.Flag--red.Flag--unflagged .Flag__value {
    color: hsl(204, 6%, 55%);
}
.Flag--green .Flag__icon svg {
    fill: hsl(105, 52%, 58%);
}
.Flag--green .Flag__icon:hover svg {
    fill: hsl(105, 52%, 48%);
}
.Flag--green .Flag__value {
    color: hsl(105, 52%, 58%);
}
.Flag--red .Flag__icon svg {
    fill: hsl(0, 79%, 66%);
}
.Flag--red .Flag__icon:hover svg {
    fill: hsl(0, 79%, 56%);
}
.Flag--red .Flag__value {
    color: hsl(0, 79%, 66%);
}
