.FormTextfield__label {
        color: hsl(204, 6%, 55%);
        display: block;
        font: 600 16px / 150% "Sailec", sans-serif;
        line-height: 135%;
        margin-bottom: 12px;
    }
.FormTextfield__input {
        border-radius: 3px;
        border: 2px solid hsl(204, 6%, 88%);
        color: hsl(204, 6%, 45%);
        font: normal 16px / 150% "Sailec", sans-serif;
        padding: 12px;
        width: 100%;
    }
.FormTextfield__input::placeholder {
        color: hsl(204, 6%, 88%);
    }
.FormTextfield--large .FormTextfield__input {
    font: 600 19px / 150% "Sailec", sans-serif;
    padding: 12px;
}
.FormTextfield[disabled] {
    opacity: 0.5;
}
.FormTextfield--hidden {
    display: none;
}

