.FlightCard {
    background-repeat: no-repeat;
    background-size: cover;
    height: 0;
    max-width: 100%;
    overflow: hidden;
    padding-bottom: 58%;
    position: relative;
}
.FlightCard__wrapper {
        align-items: center;
        display: flex;
        height: 100%;
        justify-content: center;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        padding: calc(12px * 2);
    }
@media (max-width: 34em) {
    .FlightCard__wrapper {
        padding: calc(12px * 5);
    }
}
.FlightCard__title {
        color: hsl(0, 0%, 100%);
        font: 600 19px / 150% "Sailec", sans-serif;
        text-align: center;
        text-shadow: 0 1px 0 rgba(0, 0, 0, 0.3);
    }
.FlightCard__title:hover {
        color: hsla(0, 0%, 100%, 0.9);
    }
