.FormSearchField {
    display: flex;
    align-items: center;
    position: relative;
    width: 100%;
}
.FormSearchField .FormSearchField__input {
        transition: all 0.15s linear;
    }
.FormSearchField__input {
        background: rgba(255, 255, 255, .2);
        box-shadow: 0 18px 48px rgba(14, 18, 23, .2);
        outline: 0;
        -webkit-appearance: none;
        font: 600 19px / 150% "Sailec", sans-serif;
        padding: 5px 40px calc(12px / 3) 50px;
        color: hsl(0, 0%, 100%);
        border: 0;
        width: 100%;
        height: 50px;
    }
.FormSearchField__input:hover,
    .FormSearchField__input:focus {
        color: #000;
    }
.FormSearchField:hover .FormSearchField__input,
        .FormSearchField__input:focus {
            background: hsl(0, 0%, 100%);
        }
.FormSearchField ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color: hsl(0, 0%, 100%);
        }
.FormSearchField :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: hsl(0, 0%, 100%);
            opacity:  1;
        }
.FormSearchField ::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: hsl(0, 0%, 100%);
            opacity: 1;
        }
.FormSearchField :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: hsl(0, 0%, 100%);
        }
.FormSearchField ::-ms-input-placeholder { /* Microsoft Edge */
            color: hsl(0, 0%, 100%);
        }
.FormSearchField:hover ::-webkit-input-placeholder { /* WebKit, Blink, Edge */
            color: hsl(204, 6%, 45%);
        }
.FormSearchField:hover :-moz-placeholder { /* Mozilla Firefox 4 to 18 */
            color: hsl(204, 6%, 45%);
            opacity:  1;
        }
.FormSearchField:hover ::-moz-placeholder { /* Mozilla Firefox 19+ */
            color: hsl(204, 6%, 45%);
            opacity: 1;
        }
.FormSearchField:hover :-ms-input-placeholder { /* Internet Explorer 10-11 */
            color: hsl(204, 6%, 45%);
        }
.FormSearchField:hover ::-ms-input-placeholder { /* Microsoft Edge */
            color: hsl(204, 6%, 45%);
        }
.FormSearchField__icon {
        padding-left: 12px;
        padding-top: 3px;
        position: absolute;
        top: 8px;
        left: 0;
        cursor: pointer;
    }
.FormSearchField__icon svg {
            fill: hsl(0, 0%, 100%);
        }
.FormSearchField__input:hover + .FormSearchField__icon svg,
        .FormSearchField__input:focus + .FormSearchField__icon svg,
        .FormSearchField__icon:hover svg {
            fill: hsl(0, 79%, 56%);
        }