.Paginator {
    width: 100%;
    display: flex;
    justify-content: center;
}
.Paginator__button {
    padding: 12px calc(12px * 2);
    background: hsl(204, 6%, 88%);
    border-radius: 3px;
    font: normal 16px / 150% "Sailec", sans-serif;
    line-height: 100%;
    color: hsl(204, 6%, 55%);
}
.Paginator__button:hover {
    background: hsl(204, 6%, 72%);
}
.Paginator__button {
    margin-right: 12px;
}
.Paginator__button--disabled {
    opacity: 0.25;
}
