.FrontpageSearchItem {
    float: left;
    width: 100%;
    color: hsl(205, 82%, 57%);
    padding-left: calc(12px * 4);
    margin-top: calc(12px / 2);
    font-size: 14px;
}
.FrontpageSearchItem__badge {
        display: inline-block;
        width: auto;
        margin-left: calc(12px / 3);
        color: hsl(0, 0%, 100%);
        height: 18px;
        padding: 0 calc(12px / 3);
        border-radius: 20px;
        min-width: 20px;
        line-height: 20px;
        font-size: 10px;
        text-align: center;
        position: relative;
        top: -2px;
        font-weight: 600;
    }
.FrontpageSearchItem__badge--orange {
            background: hsl(29, 100%, 62%);
        }
.FrontpageSearchItem__badge--blue {
            background: hsl(205, 82%, 57%);
        }
.FrontpageSearchItem__badge--red {
            background: hsl(0, 79%, 66%);
        }
.FrontpageSearchItem__badge--green {
            background: hsl(105, 52%, 58%);
        }
.FrontpageSearchItem__badge--purple {
            background: hsl(245, 51%, 68%);
        }
.FrontpageSearchItem__badge--yellow {
            background: hsl(35, 100%, 42%);
        }
